import style from "../Academics.module.css";
import { useTranslation } from "react-i18next";
import SelectedTopic from "./SelectedTopic";
import JoinFormTextArea from "../../../../../../JoiningForm/Student/Form/JoinFormTextArea";
import JoinFormInput from "../../../../../../JoiningForm/Student/Form/JoinFormInput";
import { useFileUpload } from "../../../../../../optimized/Hooks/ApiHooks/Auth/auth-api";
import QvipleLoader from "../../../../../../optimized/Loader/QvipleLoader";
import { imageShowUrl } from "../../../../../../optimized/Services/UrlConfig/BaseUrl";
