const SaveRecord = ({
  topic,
  state,
  setState,
  // isEdit,
  topicId,
  setTopicId,
}) => {
  const { t } = useTranslation();
  const [documentUrl, setDocumentUrl] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const [fileUpload] = useFileUpload();
  // const onTabChange = (tabKey) => {
  //   setState((pre) => ({
  //     ...pre,
  //     rec_status: tabKey,
  //   }));
  // };

  useEffect(() => {
    if (state?.files?.length > 0) {
      let arr = [];
      for (let dt of state?.files) {
        arr.push(dt?.documentKey);
      }
      setDocumentUrl(arr);
    }
  }, [state?.files?.length]);
  const onFileChange = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            files: [
              ...prev.files,
              {
                documentType: e.target.files[0]?.type,
                documentName: e.target.files[0]?.name,
                documentSize: e.target.files[0]?.size,
                documentKey: res.data?.imageKey,
              },
            ],
          }));
          setDocumentUrl((prev) => [...prev, res.data?.imageKey]);
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onRemoveImage = (imgKey) => {
    setState((prev) => ({
      ...prev,
      files: prev.files?.filter((ft) => ft?.documentKey !== imgKey),
    }));
    setDocumentUrl((prev) => (prev = prev?.filter((dt) => dt !== imgKey)));
  };

  // console.info("Sdf,mbds", documentUrl, state.files);
  return (
    <>
      {/* <div className={style.record_tab_container}>
        {tabs?.map((tab) => (
          <div
            className={
              state.rec_status === tab?.key
                ? `${style.record_tab} ${style.record_tab_active}`
                : style.record_tab
            }
            key={tab?.id}
            onClick={() => onTabChange(tab?.key)}
          >
            <h6>{t(tab?.name)}</h6>
          </div>
        ))}
      </div> */}
      <SelectedTopic topic={topic} topicId={topicId} setTopicId={setTopicId} />
      <JoinFormTextArea
        labelText={t("additional_info_optional")}
        placeholder={t("additional_info_optional_placeholder")}
        name={"additiona_info"}
        value={state.updateDescription}
        type={"text"}
        onChange={(e) =>
          setState((pre) => ({
            ...pre,
            updateDescription: e.target.value,
          }))
        }
        customStyleContainer={{
          width: "100%",
        }}
      />
      <JoinFormInput
        labelText={t("link")}
        placeholder={t("academic_record_link")}
        name={"yt_link"}
        value={state.yt_link}
        type={"text"}
        onChange={(e) =>
          setState((pre) => ({
            ...pre,
            yt_link: e.target.value,
          }))
        }
        customStyleContainer={{
          width: "100%",
          marginBottom: "0.5rem",
        }}
      />
      <div className={style.aditional_info_images}>
        {documentUrl?.map((keys, index) => (
          <Fragment key={keys}>
            <div className={style.img_remove_container}>
              <img src={`${imageShowUrl}/${keys}`} alt="uploaded avatar" />
              <h6 onClick={() => onRemoveImage(keys)}>Remove</h6>
            </div>

            {index + 1 === documentUrl?.length ? (
              <label htmlFor={`upload${documentUrl?.length}`}>
                <img
                  src="/images/member_tab/subject/subject-camera-icon.svg"
                  alt="camera icon"
                  style={{
                    cursor: "pointer",
                  }}
                />
                <input
                  multiple
                  type="file"
                  name="upload"
                  id={`upload${documentUrl?.length}`}
                  style={{ display: "none" }}
                  onChange={onFileChange}
                  accept="image/gif, image/jpeg, image/png, image/*"
                />
              </label>
            ) : null}
          </Fragment>
        ))}
        {documentUrl?.length > 0 ? null : (
          <label htmlFor={`upload${documentUrl?.length}`}>
            <img
              src="/images/member_tab/subject/subject-camera-icon.svg"
              alt="camera icon"
              style={{
                cursor: "pointer",
              }}
            />
            <input
              multiple
              type="file"
              name="upload"
              id={`upload${documentUrl?.length}`}
              style={{ display: "none" }}
              onChange={onFileChange}
              accept="image/gif, image/jpeg, image/png, image/*"
            />
          </label>
        )}
      </div>

      {disabled && <QvipleLoader />}
    </>
  );
};

export default SaveRecord;
