import { useTranslation } from "react-i18next";
import { useSubjectCoAttainmentList } from "../../Hooks/ApiHooks/SubjectManager/subject-manager-api";
import CustomSelectCo from "../../Ui/Select/CustomSelectCo";

const CoDorpdown = ({
  subjectId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const { subjectCoAttainmentList } = useSubjectCoAttainmentList({
    data: {
      sid: subjectId,
    },
    skip: !subjectId,
  });
  return (
    <>
      <CustomSelectCo
        selectLabel={t("course_outcome")}
        selectedValue={t("select_course_outcome_placeholder")}
        options={
          subjectCoAttainmentList?.length > 0 ? subjectCoAttainmentList : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />
    </>
  );
};

export default CoDorpdown;
