import React, { useEffect, useState } from "react";
import { useGetOneSubjectProfile } from "../../../../../../hooks/member_tab/subject-api";
import PopupWrapper from "../../../FinanceManager/FeesStructure/PopupWrapper";
import style from "../Academics.module.css";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import QLoader from "../../../../../../Loader/QLoader";
import JoinFormInput from "../../../../../../JoiningForm/Student/Form/JoinFormInput";
import { useTranslation } from "react-i18next";
import { useSubjectTeachingPlanSetting } from "../../../../../../optimized/Hooks/ApiHooks/SubjectManager/subject-manager-api";
import CreateButton from "../../../../../../optimized/Ui/Button/CreateButton";
import FormInput from "../../../../../../optimized/Ui/Input/FormInput";
import FormTextarea from "../../../../../../optimized/Ui/Input/FormTextarea";

const AcademicEditTime = ({ sid, onClose }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    allotted_lecture: "",
    total_hours: "",
    hours_per_weak: "",
    course_objective: "",
    course_outcome: "",
    web_reference: "",
    book_reference: "",
  });
  const [subjectTeachingPlanSetting] = useSubjectTeachingPlanSetting();
  const { getOneSubjectProfile, getOneSubjectProfileRefetch } =
    useGetOneSubjectProfile({
      sid: sid,
      skip: !sid,
    });

  useEffect(() => {
    if (sid) {
      getOneSubjectProfileRefetch();
    }
  }, [sid, getOneSubjectProfileRefetch]);

  useEffect(() => {
    if (getOneSubjectProfile?.one_subject) {
      setState({
        allotted_lecture:
          getOneSubjectProfile?.one_subject?.allotted_lecture ?? "",
        total_hours: getOneSubjectProfile?.one_subject?.total_hours ?? "",
        hours_per_weak: getOneSubjectProfile?.one_subject?.hours_per_weak ?? "",
        course_objective:
          getOneSubjectProfile?.one_subject?.course_objective ?? "",
        course_outcome: getOneSubjectProfile?.one_subject?.course_outcome ?? "",
        web_reference: getOneSubjectProfile?.one_subject?.web_reference ?? "",
        book_reference: getOneSubjectProfile?.one_subject?.book_reference ?? "",
      });
    }
  }, [getOneSubjectProfile?.one_subject]);

  const onNumberChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onUpdate = () => {
    if (sid) {
      setDisabled((pre) => !pre);
      subjectTeachingPlanSetting({
        sid: sid,
        bodyParameter: state,
      })
        .then(() => {
          getOneSubjectProfileRefetch();
          onClose();
        })
        .catch({});
    }
  };

  return (
    <PopupWrapper onClose={onClose}>
      <div
        className={style.add_academic_record}
        style={{
          minWidth: "42rem",
          maxWidth: "42rem",
        }}
      >
        <div className={style.add_academic_record_title}>
          <div
            className={style.add_academic_record_text}
            style={{
              justifyContent: "space-between",
              width: "100%",
            }}
          >
            <h6>{t("edit_academic")}</h6>
            <img
              src="/images/close-post-icon.svg"
              onClick={onClose}
              alt=""
              style={{
                cursor: "pointer",
              }}
            />
          </div>
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
            marginTop: "0.5rem",
          }}
        />
        <div
          className={style.academic_edit_container}
          style={{
            gap: "2%",
          }}
        >
          <FormInput
            label={t("allotted_lecture")}
            placeholder={t("allotted_lecture_placeholder")}
            name={"allotted_lecture"}
            value={state.allotted_lecture}
            type={"text"}
            onChange={onNumberChange}
            customFormInputContainer={{
              width: "32%",
            }}
          />
          <FormInput
            label={t("total_hours")}
            placeholder={t("total_hours_placeholder")}
            name={"total_hours"}
            value={state.total_hours}
            type={"text"}
            onChange={onNumberChange}
            customFormInputContainer={{
              width: "32%",
            }}
          />
          <FormInput
            label={t("hours_per_weak")}
            placeholder={t("hours_per_weak_placeholder")}
            name={"hours_per_weak"}
            value={state.hours_per_weak}
            type={"text"}
            onChange={onNumberChange}
            customFormInputContainer={{
              width: "32%",
            }}
          />
        </div>

        <FormTextarea
          label={t("course_objective")}
          placeholder={t("course_objective_placeholder")}
          name={"course_objective"}
          value={state.course_objective}
          type={"text"}
          onChange={onInputChange}
        />
        <FormTextarea
          label={t("course_outcome")}
          placeholder={t("course_outcome_placeholder")}
          name={"course_outcome"}
          value={state.course_outcome}
          type={"text"}
          onChange={onInputChange}
        />
        <FormTextarea
          label={t("web_reference")}
          placeholder={t("web_reference_placeholder")}
          name={"web_reference"}
          value={state.web_reference}
          type={"text"}
          onChange={onInputChange}
        />
        <FormTextarea
          label={t("book_reference")}
          placeholder={t("book_reference_placeholder")}
          name={"book_reference"}
          value={state.book_reference}
          type={"text"}
          onChange={onInputChange}
        />
        <CreateButton label="update" onAction={onUpdate} />
      </div>
      {disabled && <QLoader />}
    </PopupWrapper>
  );
};

export default AcademicEditTime;

